.tower {
    display: flex;
    flex-direction: column;
    width: 250px;
    margin: auto;
    margin-top: 10%;
}

.up {
    display: inherit;
}

.up div.floor {
    position: relative;
    width: 100%;
    height: 80px;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
}

.up button {
    background-color: rgb(49, 150, 175);
    border: 1px solid rgb(21, 87, 104);
    outline: none;
}

.building {
    width: 80vw;
    height: 80vh;
    margin: auto;
}
.btn_nav {
    position: absolute;
    bottom: 50px;
    right: 60px;
    display: flex;
    flex-direction: column-reverse;
    width: max-content;
    margin: auto;
    margin-top: 2em;
    border: 1px solid black;
    padding: 6px;
}
.btn_nav button {
    /* outline: none;
    border-radius: 50%;
    margin-bottom: 6px;
    background: black;
    color: white;
    cursor: pointer;
    padding: 10px 15px;
    font-size: 17px; */
    width: 45px;
    height: 44px;
    outline: none;
    border-radius: 50%;
    margin-bottom: 6px;
    background: #1d1c1c;
    color: #fff;
    cursor: pointer;
    font-size: 17px;
    box-shadow: 2px 4px 10px inset #b4b9b5, -2px -3px 4px inset #100f10;
}

.btn_nav button:hover {
    background: #1d1c1c;
    box-shadow: 2px 4px 10px inset #d3dad4, -2px -3px 4px inset #000000;
    font-size: 16px;
    transition: all linear .2s;
}

span.curent-c{
    padding: 0px 22px;
    background: #0bb330;
    color: #000;
    font-family: monospace;
    border: 4px solid #000;
    font-size: 46px;
}
.curent-c-trv {
    padding: 0px 22px;
    background: #0cde3a;
    color: #000;
    font-family: monospace;
    border: 4px solid #000;
    font-size: 46px;
}
.box {
    position: relative;
    background: rgb(0, 0, 0);
    width: 30%;
    height: 85%;
    margin: auto;
}
.box::before {
    content: '';
    position: absolute;
    left: 47%;
    width: 4px;
    height: 100%;
    background: white;
    z-index: 99;
}

.statePlace {
  position: fixed;
  top: 10px;
  left: 20px;
  text-transform: uppercase;
  font-weight: bold;
}

.btn-state {
    background: rgb(226, 215, 215);
    width: 8px;
    height: 7px;
    border-radius: 50%;
    position: absolute;
    top: 10px;
    right: 10px;
}

.btn-state-up {
    background: blue;
}

.btn-state-down {
    background: red;
}

.destination {
    font-weight: bold;
    position: absolute;
    top: 10px;
    right: 20px;
    text-transform: uppercase;
}